'use server';

import { phishingUrlDetector, PhishingUrlDetectorOutput } from '@/ai/flows/phishing-url-detector';
import { z } from 'zod';

const formSchema = z.object({
  url: z.string({
    required_error: "URL is required.",
  }).url("Please enter a valid URL."),
  apiKey: z.string().min(1, "API Key is required."), // For simulation
});

export type FormState = {
  result?: PhishingUrlDetectorOutput;
  error?: string;
  success: boolean;
  message?: string;
}

export async function checkPhishingUrl(
  prevState: FormState,
  formData: FormData
): Promise<FormState> {
  try {
    const validatedFields = formSchema.safeParse({
      url: formData.get('url'),
      apiKey: formData.get('apiKey'),
    });

    if (!validatedFields.success) {
      return {
        success: false,
        error: validatedFields.error.errors.map((e) => e.message).join(', '),
      };
    }

    const { url } = validatedFields.data;

    // Here you would typically validate the API key against a database
    // For this example, we'll just check if it's not empty.

    const result = await phishingUrlDetector({ url });

    return { 
      success: true,
      result 
    };

  } catch (e) {
    const errorMessage = e instanceof Error ? e.message : 'An unexpected error occurred.';
    return { 
      success: false,
      error: `Analysis failed: ${errorMessage}` 
    };
  }
}
