"use client";

import { useEffect, useRef, useActionState } from "react";
import { useFormStatus } from "react-dom";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import { z } from "zod";
import { checkPhishingUrl, FormState } from "@/app/actions";
import { Button } from "@/components/ui/button";
import {
  Card,
  CardContent,
  CardDescription,
  CardFooter,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Progress } from "@/components/ui/progress";
import { Badge } from "@/components/ui/badge";
import { Skeleton } from "@/components/ui/skeleton";
import { useToast } from "@/hooks/use-toast";
import { AlertTriangle, Lock, Search, Shield, ShieldCheck, Info } from "lucide-react";

const formSchema = z.object({
  url: z.string().url("Please enter a valid URL."),
  apiKey: z.string().min(1, "API Key is required."),
});

function SubmitButton() {
  const { pending } = useFormStatus();
  return (
    <Button type="submit" className="w-full" disabled={pending}>
      {pending ? (
        <>
          <svg className="animate-spin -ml-1 mr-3 h-5 w-5 text-white" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
            <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
            <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
          </svg>
          Analyzing...
        </>
      ) : (
        <>
          <Search className="mr-2 h-4 w-4" />
          Analyze URL
        </>
      )}
    </Button>
  );
}

export default function Detector() {
  const { toast } = useToast();
  const formRef = useRef<HTMLFormElement>(null);
  
  const initialState: FormState = { success: false };
  const [state, formAction] = useActionState(checkPhishingUrl, initialState);

  const form = useForm({
    resolver: zodResolver(formSchema),
    defaultValues: {
      url: "",
      apiKey: "",
    },
  });

  useEffect(() => {
    if (!state.success && state.error) {
      toast({
        variant: "destructive",
        title: "Analysis Failed",
        description: state.error,
      });
    }
  }, [state, toast]);

  return (
    <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
      <Card>
        <form
          ref={formRef}
          action={formAction}
          className="flex flex-col h-full"
        >
          <CardHeader>
            <CardTitle>URL Phishing Detector</CardTitle>
            <CardDescription>
              Enter a URL and an API key to check if it's a phishing site.
            </CardDescription>
          </CardHeader>
          <CardContent className="space-y-4 flex-grow">
            <div className="space-y-2">
              <Label htmlFor="url">URL to Analyze</Label>
              <Input
                id="url"
                name="url"
                placeholder="https://example.com"
                required
                type="url"
              />
            </div>
            <div className="space-y-2">
              <Label htmlFor="apiKey">API Key</Label>
              <div className="relative">
                <Lock className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-muted-foreground" />
                <Input
                  id="apiKey"
                  name="apiKey"
                  placeholder="Your-API-Key"
                  required
                  type="password"
                  className="pl-10"
                />
              </div>
            </div>
          </CardContent>
          <CardFooter>
            <SubmitButton />
          </CardFooter>
        </form>
      </Card>

      <Card>
        <CardHeader>
          <CardTitle>Analysis Result</CardTitle>
          <CardDescription>
            The prediction from our model will appear here.
          </CardDescription>
        </CardHeader>
        <CardContent>
          {useFormStatus().pending ? (
            <div className="space-y-6">
              <div className="flex justify-between items-center">
                <Skeleton className="h-6 w-24" />
                <Skeleton className="h-6 w-32" />
              </div>
              <div className="space-y-2">
                <Skeleton className="h-4 w-1/3" />
                <Skeleton className="h-10 w-full" />
              </div>
              <div className="space-y-2">
                <Skeleton className="h-4 w-1/4" />
                <Skeleton className="h-20 w-full" />
              </div>
            </div>
          ) : state.result ? (
            <div className="space-y-6">
              <div className="flex justify-between items-center text-lg font-semibold">
                <span>Prediction</span>
                <Badge
                  variant={
                    state.result.prediction === "phishing"
                      ? "destructive"
                      : "default"
                  }
                  className="text-base"
                >
                  {state.result.prediction === "phishing" ? (
                    <AlertTriangle className="mr-2 h-4 w-4" />
                  ) : (
                    <ShieldCheck className="mr-2 h-4 w-4" />
                  )}
                  {state.result.prediction.charAt(0).toUpperCase() +
                    state.result.prediction.slice(1)}
                </Badge>
              </div>

              <div className="space-y-2">
                <Label>Confidence Score</Label>
                <div className="flex items-center gap-4">
                  <Progress value={state.result.confidenceScore * 100} className="w-full" />
                  <span className="font-mono text-lg font-semibold">
                    {(state.result.confidenceScore * 100).toFixed(0)}%
                  </span>
                </div>
              </div>

              <div className="space-y-2">
                <Label>AI-Powered Reasoning</Label>
                <div className="p-4 bg-muted rounded-lg border">
                  <p className="text-sm text-muted-foreground italic">
                    {state.result.reasoning}
                  </p>
                </div>
              </div>
            </div>
          ) : (
             <div className="flex flex-col items-center justify-center text-center text-muted-foreground p-8 space-y-4 border-2 border-dashed rounded-lg h-full">
                <Shield className="w-16 h-16" />
                <p className="text-lg font-medium">Results will be displayed here</p>
                <p className="text-sm">Submit a URL to start the analysis.</p>
              </div>
          )}
        </CardContent>
      </Card>
    </div>
  );
}
